@import url(https://fonts.googleapis.com/css?family=Raleway:100,200,300,400);
.header-container {
	height: 100vh;
	display: -webkit-flex;
	display: flex;
	background-image: -webkit-linear-gradient(#a256ca, #efefef);
	background-image: linear-gradient(#a256ca, #efefef);
}
.header-content {
	display: -webkit-flex;
	display: flex;
	width: 100%;
	max-width: 1000px;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-direction: row;
	        flex-direction: row;
	margin: 0 auto;
}
.header-right {
	width: 100%;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
}

.header-left img {
	max-width: 100%;
}
.title {
	color: #64008a;
	font-weight: 200;
	font-size: 50pt;
}

.header-right img {
	max-width: 200px;
	max-height: 200px;
	-webkit-filter: drop-shadow(5px 5px 5px #aaa);
	filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, .3));
}

@media only screen and (max-width: 600px) {
	.header-content {
		-webkit-flex-wrap: wrap;
		        flex-wrap: wrap;
	}
	.header-right img {
		max-width: 100px;
		max-height: 100px;
	}
	.header-left {
		max-width: 80%;
		-webkit-justify-content: center;
		        justify-content: center;
		-webkit-align-items: center;
		        align-items: center;
	}
	.title {
		font-size: 30pt;
	}
}

.block-container {
	display: -webkit-flex;
	display: flex;
	padding: 72px 20px;
}
.block-content {
	display: -webkit-flex;
	display: flex;
	width: 100%;
	max-width: 1000px;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-direction: row;
	        flex-direction: row;
	margin: 0 auto;
}

.left-block {
	width: 150%;
}
.left-block img {
	max-width: 100%;
}

@media only screen and (max-width: 600px) {
	.block-content {
		-webkit-flex-wrap: wrap;
		        flex-wrap: wrap;
	}
	.left-block {
		width: 50%;
	}
}

.right-block {
	padding: 0px 20px;
}
.right-block h2 {
	text-align: center;
	color: #64008a;
	font-weight: 200;
	font-size: 40pt;
	padding-bottom: 15px;
}
.right-block p {
	font-size: 15pt;
	text-align: center;
}

.footer-content {
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	padding: 72px 20px;
	background: #efefef;
}
.input {
	width: 100%;
	padding: 0px 10px;
	border: 2px solid #64008a;
	color: #64008a;
	margin-bottom: 15px;
	height: 44px;
	border-radius: 25px;
	font-size: 13pt;
}

.input::-webkit-input-placeholder {
	/* Chrome, Firefox, Opera, Safari 10.1+ */
	color: #64008a;
	font-size: 13pt;
}

.input::-ms-input-placeholder {
	/* Chrome, Firefox, Opera, Safari 10.1+ */
	color: #64008a;
	font-size: 13pt;
}

.input::placeholder {
	/* Chrome, Firefox, Opera, Safari 10.1+ */
	color: #64008a;
	font-size: 13pt;
}

.form {
	max-width: 1000px;
	width: 100%;
}
.form textarea {
	width: 100%;
	padding: 5px 20px;
	border-radius: 25px;
	border: 1px solid #fff;
	background: #64008a;
}

* {
	margin: 0;
	padding: 0;
	outline: 0;
	box-sizing: border-box;
	font-family: "Quicksand", sans-serif;
	list-style-type: none;
	font-family: 'Raleway', sans-serif;
}
body {
	background: #fff;
}

.news {
	display: -webkit-flex;
	display: flex;
	padding: 72px 20px;
	-webkit-flex-direction: column;
	        flex-direction: column;
}
.news h1 {
	text-align: center;
	padding-bottom: 40px;
	color: #64008a;
	font-weight: 200;
	font-size: 30pt;
}
.new-item {
	display: -webkit-flex;
	display: flex;
	width: 100%;
	max-width: 1000px;
	-webkit-flex-direction: row;
	        flex-direction: row;
	margin: 10px auto;
}
.new-item a {
	box-shadow: 5px 5px 10px #848484;
	padding: 20px;
	width: 100%;
	border-top-left-radius: 25px;
	border-bottom-right-radius: 25px;
	text-decoration: none;
}
.new-item a h2 {
	color: rgba(0, 0, 0, .6);
	padding-bottom: 10px;
}
.new-item a strong {
	color: rgba(0, 0, 0, .4);
	padding: 10px 0px;
}

@media only screen and (max-width: 600px) {
	.block-content {
		-webkit-flex-wrap: wrap;
		        flex-wrap: wrap;
	}
	.left-block {
		width: 50%;
	}
}

