.footer-content {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 72px 20px;
	background: #efefef;
}
.input {
	width: 100%;
	padding: 0px 10px;
	border: 2px solid #64008a;
	color: #64008a;
	margin-bottom: 15px;
	height: 44px;
	border-radius: 25px;
	font-size: 13pt;
}

.input::placeholder {
	/* Chrome, Firefox, Opera, Safari 10.1+ */
	color: #64008a;
	font-size: 13pt;
}

.form {
	max-width: 1000px;
	width: 100%;
}
.form textarea {
	width: 100%;
	padding: 5px 20px;
	border-radius: 25px;
	border: 1px solid #fff;
	background: #64008a;
}
